import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../Ui/Tab/Horizontal/HorizontalTabCard";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import style from "../FinanceManager/FinanceManager.module.css";
import { Suspense, lazy, useCallback, useEffect } from "react";
import { assestsUtilityUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import StaffLeaveRequest from "./StaffLeaveRequest/StaffLeaveRequest";
import { useOneStaffProfile } from "../../../hooks/institute/institute-staff-student-api";
import QvipleLoader from "../../Loader/QvipleLoader";

const LmsStaffOverview = lazy(() =>
  import("../LmsManager/LmsStaffOverview/LmsStaffOverview")
);
const StaffCOffLeave = lazy(() => import("./StaffCOffLeave/StaffCOffLeave"));
const StaffLeave = ({ sid }) => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();

  const { oneStaffProfile, oneStaffProfileRefetch } = useOneStaffProfile({
    sid: sid,
    skip: !sid,
  });

  useEffect(() => {
    if (sid) {
      oneStaffProfileRefetch();
    }
  }, [sid, oneStaffProfileRefetch]);
  const onNavigate = useCallback(() => {
    let url = `/q/${params.username}/member?a=leave`;
    navigate(url, {
      state: getQuery.state,
    });
  }, [navigate, getQuery.state, params.username]);

  // console.info("oneStaffProfile", oneStaffProfile);
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <div className={style.fm_header_start_container}>
            <img
              alt="search icon"
              src={`${assestsUtilityUrl}/back.svg`}
              style={{
                height: "2rem",
                width: "2rem",
              }}
              onClick={onNavigate}
            />
            <h6>{t("leave_request")}</h6>
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <HorizontalTabWrapper>
        {tabList.map((tab) => (
          <HorizontalTabCard
            key={tab?.id}
            tab={tab}
            isLink
            isActive={tab.groupIs?.includes(searchParams.get("n"))}
            parentState={{
              ...getQuery.state,
              staffId: sid,
              urlOffSet: "Staff",
            }}
            url={`/q/${params.username}/member?a=leave${tab.urlKey}`}
          />
        ))}
      </HorizontalTabWrapper>

      <BorderBottom
        customStyleBorder={{
          marginBlock: "0",
        }}
      />
      <UserMemberContentWrapper>
        {!searchParams.get("n") ? (
          <StaffLeaveRequest
            sid={sid}
            leaveTypeToggle={
              oneStaffProfile?.staff?.institute?.lms_depart?.[0]?.leave_manage
            }
            leaveConfig={{
              casual_leave: oneStaffProfile?.staff?.casual_leave,
              medical_leave: oneStaffProfile?.staff?.medical_leave,
              sick_leave: oneStaffProfile?.staff?.sick_leave,
              c_off_leave: oneStaffProfile?.staff?.c_off_leave,

              commuted_leave: oneStaffProfile?.staff?.commuted_leave,
              maternity_leave: oneStaffProfile?.staff?.maternity_leave,
              paternity_leave: oneStaffProfile?.staff?.paternity_leave,
              quarantine_leave: oneStaffProfile?.staff?.quarantine_leave,
              half_pay_leave: oneStaffProfile?.staff?.half_pay_leave,
              study_leave: oneStaffProfile?.staff?.study_leave,
              sabbatical_leave: oneStaffProfile?.staff?.sabbatical_leave,
              special_disability_leave:
                oneStaffProfile?.staff?.special_disability_leave,
              winter_vacation_leave:
                oneStaffProfile?.staff?.winter_vacation_leave,
              summer_vacation_leave:
                oneStaffProfile?.staff?.summer_vacation_leave,
              child_adoption_leave:
                oneStaffProfile?.staff?.child_adoption_leave,
              bereavement_leave: oneStaffProfile?.staff?.bereavement_leave,
            }}
          />
        ) : searchParams.get("n") === "overview" ? (
          <Suspense fallback={<QvipleLoader />}>
            <LmsStaffOverview sid={sid} isStaff />
          </Suspense>
        ) : searchParams.get("n") === "coff" ? (
          <Suspense fallback={<QvipleLoader />}>
            <StaffCOffLeave sid={sid} />
          </Suspense>
        ) : null}
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};

export default StaffLeave;

const tabList = [
  {
    label: "request",
    id: "request",
    urlKey: "",
    groupIs: ["", null],
  },
  {
    label: "overview",
    id: "overview",
    urlKey: "&n=overview&category=All Leave",
    groupIs: ["overview"],
  },
  {
    label: "c_off_leave",
    id: "c_off_leave",
    urlKey: "&n=coff",
    groupIs: ["coff"],
  },
];
