import {
  assestsUserFinanceUrl,
  assestsUserLmsUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import dayjs from "dayjs";
import { localTimeFormat } from "../../../Utils/Functions/localTime";
import { pdfCdnUrl } from "../../../Services/UrlConfig/BaseUrl";
import style from "../../LmsManager/LmsManager.module.css";
import { useTranslation } from "react-i18next";
import StaffLeaveRequestCardMenu from "./StaffLeaveRequestCardMenu";
import { useState } from "react";
const StaffLeaveRequestCard = ({ leave, onRefetch }) => {
  const { t } = useTranslation();
  const [menu, setMenu] = useState(false);

  const onToggle = () => {
    setMenu((pre) => !pre);
  };
  return (
    <>
      <div className={style.lmsr_card}>
        <div className={style.lmsrc_card}>
          <div className={style.lmsrc_card_inner}>
            <img
              src={`${assestsUserLmsUrl}/leave-raise.svg`}
              alt="leave icon"
            />
            <div className={style.lmsrci_text}>
              <h6
                style={{
                  fontWeight: "400",
                }}
              >
                {t("leave_type")} {" : "}
                {leave?.leave_type}
              </h6>
              <p>
                {t("raised_on")} {localTimeFormat(leave?.createdAt, "LT")},{" "}
                {dayjs(leave?.createdAt).format("DD MMM YYYY")}
              </p>
              <p>
                {t("for_dates")} {" : "}
                {leave?.date?.map((dt) => (
                  <span key={dt}>
                    {dt}, {` `}
                  </span>
                ))}
              </p>
            </div>
          </div>
          <div className={style.lmsrc_btn}>
            <div className={style.lmsrci_text}>
              <p>
                {t("status")} {" : "}
                <span
                  style={{
                    color:
                      leave?.status === "Request"
                        ? "rgba(18, 18, 18, 0.7)"
                        : leave?.status === "Rejected"
                        ? "red"
                        : "#14ab50",
                  }}
                >
                  {leave?.status === "Request"
                    ? "Requested"
                    : leave?.status === "Rejected"
                    ? "Rejected"
                    : "Approved"}
                </span>
              </p>
            </div>
            {leave?.status === "Accepted" ? null : (
              <img
                src={`${assestsUserFinanceUrl}/menu.svg`}
                alt="menu icons"
                onClick={onToggle}
                style={{
                  cursor: "pointer",
                  height: "1.3rem",
                  width: "auto",
                }}
              />
            )}
          </div>
        </div>
        {leave?.recommend?.recommend_by?.staffFirstName ? (
          <div
            className={style.lmsrci_text}
            style={{
              marginTop: "0.5rem",
            }}
          >
            <p>
              {t("recomend_on")}{" "}
              {localTimeFormat(leave?.recommend?.recommend_on, "LT")},{" "}
              {dayjs(leave?.recommend?.recommend_on).format("DD MMM YYYY")}
              {", "}
              {t("recomending_by")}
              {`${leave?.recommend?.recommend_by?.staffFirstName ?? ""} ${
                leave?.recommend?.recommend_by?.staffMiddleName ?? ""
              } ${leave?.recommend?.recommend_by?.staffLastName ?? ""}`}
            </p>
          </div>
        ) : null}

        {leave?.review?.review_by?.staffFirstName ? (
          <div
            className={style.lmsrci_text}
            style={{
              marginTop: "0.5rem",
            }}
          >
            <p>
              {t("reviewed_on")}{" "}
              {localTimeFormat(leave?.review?.review_on, "LT")},{" "}
              {dayjs(leave?.review?.review_on).format("DD MMM YYYY")}
              {", "}
              {t("reviewing_by")}
              {`${leave?.review?.review_by?.staffFirstName ?? ""} ${
                leave?.review?.review_by?.staffMiddleName ?? ""
              } ${leave?.review?.review_by?.staffLastName ?? ""}`}
            </p>
          </div>
        ) : null}

        {leave?.sanction?.sanction_by?.staffFirstName ? (
          <div
            className={style.lmsrci_text}
            style={{
              marginTop: "0.5rem",
            }}
          >
            <p>
              {t("sanction_on")}{" "}
              {localTimeFormat(leave?.sanction?.sanction_on, "LT")},{" "}
              {dayjs(leave?.sanction?.sanction_on).format("DD MMM YYYY")}
              {", "}
              {t("sanctioning_by")}
              {`${leave?.sanction?.sanction_by?.staffFirstName ?? ""} ${
                leave?.sanction?.sanction_by?.staffMiddleName ?? ""
              } ${leave?.sanction?.sanction_by?.staffLastName ?? ""}`}
            </p>
          </div>
        ) : null}
        <div
          className={style.lmsrci_text}
          style={{
            marginTop: "0.5rem",
          }}
        >
          <h6
            style={{
              fontWeight: "400",
              whiteSpace: "break-spaces",
            }}
          >
            {t("reason")} {" : "}
            {leave?.reason}
          </h6>
        </div>
        {leave?.attach && (
          <div
            className={style.lmsrci_text}
            style={{
              marginTop: "0.5rem",
            }}
          >
            <h6
              style={{
                fontWeight: "400",
                whiteSpace: "break-spaces",
              }}
            >
              {t("attachment")} {" : "}
              <a
                href={`${pdfCdnUrl}/${leave?.attach}`}
                target="_blank"
                rel="noopener noreferrer"
                style={{
                  textDecoration: "none",
                }}
              >
                <span className={style.lmsrc_date}>{t("view_attachment")}</span>
              </a>
            </h6>
          </div>
        )}
      </div>

      {menu && (
        <StaffLeaveRequestCardMenu
          lid={leave?._id}
          onClose={onToggle}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default StaffLeaveRequestCard;
