import ModalMenu from "../../../Ui/Modal/ModalMenu";
import { useTranslation } from "react-i18next";
import { useState } from "react";
import { useLmsStaffRemoveRequestLeave } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";

const StaffLeaveRequestCardMenu = ({ onClose, lid, onRefetch }) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [lmsStaffRemoveRequestLeave] = useLmsStaffRemoveRequestLeave();

  const onDelete = () => {
    if (lid) {
      setDisabled((pre) => !pre);
      lmsStaffRemoveRequestLeave({
        lid: lid,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <ModalMenu onClose={onClose} disabled={disabled}>
        <h6 onClick={onDelete}>{t("delete")}</h6>
      </ModalMenu>
    </>
  );
};

export default StaffLeaveRequestCardMenu;
