import { useCallback, useEffect, useState } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useLmsStaffRequestLeaveList } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../Ui/Button/CreateButton";
import StaffLeaveRequestCard from "./StaffLeaveRequestCard";
import StaffNewLeaveRequest from "./StaffNewLeaveRequest";

const StaffLeaveRequest = ({ sid, leaveConfig, leaveTypeToggle }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [addRequest, setAddRequest] = useState(false);
  const {
    lmsStaffRequestLeaveList,
    lmsStaffRequestLeaveListLoading,
    lmsStaffRequestLeaveListRefetch,
  } = useLmsStaffRequestLeaveList({
    data: {
      sid: sid,
      page: page,
      limit: 10,
      search: search,
      category: "",
    },
    skip: !sid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (lmsStaffRequestLeaveList?.list?.length) {
      let obj = nextPage(lmsStaffRequestLeaveList?.list?.length);
      setState(() => obj);
    }
  }, [lmsStaffRequestLeaveList?.list]);

  // eslint-disable-next-line react-hooks/exhaustive-deps
  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAddRequest((pre) => !pre);
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    lmsStaffRequestLeaveListRefetch();
  }, [lmsStaffRequestLeaveListRefetch]);

  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.7rem",
        }}
      >
        <section
          className={style.fm_search_full_container}
          style={{
            justifyContent: "space-between",
            gap: "1rem",
          }}
        >
          <div
            className={style.fm_search_full_container_inner}
            style={{
              width: "81%",
            }}
          >
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
          <CreateButton
            label={t("create_new_leave")}
            customStyle={{
              margin: "0",
              padding: "0.4rem 1rem",
            }}
            onAction={onToggle}
          />
        </section>
      </div>
      {lmsStaffRequestLeaveList?.list?.map((leave, index) =>
        lmsStaffRequestLeaveList?.list?.length === index + 1 ? (
          <div key={leave?._id} ref={ref}>
            <StaffLeaveRequestCard leave={leave} onRefetch={onRefetch} />
          </div>
        ) : (
          <StaffLeaveRequestCard
            leave={leave}
            key={leave?._id}
            onRefetch={onRefetch}
          />
        )
      )}
      {lmsStaffRequestLeaveListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        lmsStaffRequestLeaveList?.list?.length === 0 && (
          <EmptyMessage title="No leave request fuond related this search." />
        )
      ) : (
        lmsStaffRequestLeaveList?.list?.length === 0 && (
          <EmptyMessage title="No leave request." />
        )
      )}
      {addRequest && (
        <StaffNewLeaveRequest
          sid={sid}
          onRefetch={onRefetch}
          onClose={onToggle}
          leaveConfig={leaveConfig}
          leaveTypeToggle={leaveTypeToggle}
        />
      )}
    </>
  );
};

export default StaffLeaveRequest;
